import React from 'react'
import PropTypes from 'prop-types'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import { withStyles } from '@material-ui/core/styles'
import ArrowBackIcon from '@material-ui/icons/ArrowBack'
import Typography from '@material-ui/core/Typography'
import { Redirect } from 'react-router'
import mockData from '../../mock-data'
import TextField from '@material-ui/core/TextField'
import IconButton from '@material-ui/core/IconButton'
import SendIcon from '@material-ui/icons/Send'

const styles = theme => ({
  root: {
    width: '100%',
    height: '100vh',
    display: 'flex',
  flexDirection: 'column',
  },
  backArrowIcon: {
    color: '#000000',
  },
  header: {
    width: '100%',
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-start',
  },

  content: {
    flexGrow: 1,
    overflowY: 'auto',
  },
})

function Message(props) {
  var textAlign = 'left'
  var bgColor = 'grey'
  var marginRight = 100
  var marginLeft = 5
  if (props.message.createdByUserGroup === 'TARGET') {
    textAlign = 'right'
    bgColor = 'red'
    marginLeft = 100
    marginRight = 5
  }
  return (
    <div
      style={{
        textAlign: textAlign,
        marginLeft: marginLeft,
        marginRight: marginRight,
      }}
    >
      <p
        style={{
          backgroundColor: bgColor,
          display: 'inline-block',
          padding: 4,
          borderRadius: 10,
          color: 'white',
        }}
      >
        {props.message.body}
      </p>
    </div>
  )
}

function Messages(props) {
  return (
    <div>
      {props.messages.map(function(message, index) {
        return <Message message={message} key={index} />
      })}
    </div>
  )
}

class Conversation extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      messages: mockData.messages,
      redirectTo: 'none',
      newMessage: '',
    }
  }

  handleBackClick() {
    this.setState({
      redirectTo: '/',
    })
  }

  handleChange = event => {
    this.setState({
      messages: mockData.messages,
      redirectTo: 'none',
      newMessage: event.target.value,
    })
  }

  componentWillMount() {
    document.body.style.margin = 0
  }

  render() {
    if (this.state.redirectTo !== 'none') {
      return <Redirect to={this.state.redirectTo} />
    }
    const { classes } = this.props

    return (
      <div className={classes.root}>
        <AppBar position="static" style={{ boxShadow: 'none' }}>
          <div className={classes.header}>
            <div className={classes.backArrowIcon} flex={1}>
              <Toolbar>
                <ArrowBackIcon button = "true" onClick={() => this.handleBackClick()} />
              </Toolbar>
            </div>

            <div
              style={{
                width: '80%',
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'center',
              }}
            >
              <div>
                <Typography variant="h6" className={classes.title}>
                  Sauder Woodworking Co.
                </Typography>
                <Typography variant="subtitle2" className={classes.title}>
                  2019 ODL Cushions Plan
                </Typography>
                <Typography variant="subtitle2" className={classes.title}>
                  Red Soft Cushion
                </Typography>
              </div>
            </div>
          </div>
        </AppBar>

        <div
          className={classes.content}
          style={{ marginLeft: 10, marginRight: 10 }}
        >
          <Messages messages={this.state.messages} />
        </div>

        <div
          style={{
            display: 'flex',
            backgroundColor: '#fafafa',
            marginLeft: 10,
          }}
        >
          <TextField
            id="outlined-bare"
            value={this.state.newMessage}
            multiline
            margin="normal"
            variant="outlined"
            onChange={this.handleChange}
            style={{
              width: '80%',
              backgroundColor: 'white',
              borderColor: 'blue',
            }}
          />
          <IconButton
            className={classes.button}
            color="primary"
            aria-label="Send"
            style={{ width: '20%' }}
          >
            <SendIcon />
          </IconButton>
        </div>
      </div>
    )
  }
}

Conversation.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(Conversation)
