import React from 'react'
import PropTypes from 'prop-types'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import { withStyles } from '@material-ui/core/styles'
import ArrowBackIcon from '@material-ui/icons/ArrowBack'
import Typography from '@material-ui/core/Typography'
import { Redirect } from 'react-router'

const styles = theme => ({
    root: {
      width: '100%',
      height: '100vh',
      display: 'flex',
    flexDirection: 'column',
    },
    backArrowIcon: {
      color: '#000000',
    },
    header: {
      width: '100%',
      display: 'flex',
      flexDirection: 'row',
      justifyContent: 'flex-start',
    },
  
    content: {
      flexGrow: 1,
      overflowY: 'auto',
    },
  })

class OAuth extends React.Component {
    constructor(props) {
      super(props)
      this.state = {
        redirectTo: 'none'
      }
    }
    
    getCookie(name) {
        var nameEQ = name + "=";
        var ca = document.cookie.split(';');
        for(var i=0;i < ca.length;i++) {
            var c = ca[i];
            while (c.charAt(0)==' ') c = c.substring(1,c.length);
            if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length,c.length);
        }
        return null;
    }

    parseJwt (token) {
        var base64Url = token.split('.')[1];
        var base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
        return JSON.parse(window.atob(base64));
    }

    handleBackClick() {
        this.setState({
          redirectTo: '/',
        })
    }
    
    render() {
        const { classes } = this.props;

        console.log(this.props);
        var hash = this.props.location.hash.substr(1);
        var params = hash.split("&");
        document.cookie = params[0];
        var token = this.getCookie("access_token");
        console.log(this.getCookie("access_token"));
        var parsedToken = this.parseJwt(token);
        console.log(this.parseJwt(token));
        //return <Redirect to={this.state.redirectTo} />

       // return <h1>{parsedToken.username}</h1>

      if (this.state.redirectTo !== 'none') {
        return <Redirect to={this.state.redirectTo} />
      }

       return (
        <div className={classes.root}>
        <AppBar position="static" style={{ boxShadow: 'none' }}>
          <div className={classes.header}>
            <div className={classes.backArrowIcon} flex={1}>
              <Toolbar>
                <ArrowBackIcon button = "true" onClick={() => this.handleBackClick()} />
              </Toolbar>
            </div>

            <div
              style={{
                width: '80%',
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'center',
              }}
            >
              <div>
                <Typography variant="h6" className={classes.title}>
                  Token
                </Typography>
                <Typography variant="subtitle2" className={classes.title}>
                  Received for 
                </Typography>
                <Typography variant="subtitle2" className={classes.title}>
                  {parsedToken.username}
                </Typography>
              </div>
            </div>
          </div>
        </AppBar>
        </div>
       );
    }
  }
  
  OAuth.propTypes = {
    classes: PropTypes.object.isRequired,
  }

  export default withStyles(styles)(OAuth)

